.svg {
  width: 50px;
  height: 50px;
}

.svg > path {
  fill: none;
  stroke: blue;
  stroke-width: 20;
  stroke-dasharray: 3840px;
  stroke-dashoffset: 3840px;
  animation: ali 1s;
  animation-fill-mode: forwards;
  animation-delay: 0.3s;
}

@keyframes ali {
  from {
    stroke-dashoffset: 3840px;
  }
  to {
    stroke-dashoffset: 0px;
  }
}
