.first_lineOfCircle {
  width: 60px;
  height: 80px;
  position: absolute;

  top: 0;
  left: 30px;
}

.first_lineOfCircle > path {
  fill: none;
  stroke: red;
  stroke-width: 20;
  stroke-dasharray: 785px;
  stroke-dashoffset: 785px;
  animation: alid 0.4s;
  animation-fill-mode: forwards;
  transform: rotate(45deg);
}

.second_lineOfCircle {
  width: 60px;
  height: 80px;
  position: absolute;
  top: 35px;
  left: 30px;
}

.second_lineOfCircle > path {
  fill: none;
  stroke: red;
  stroke-width: 20;
  stroke-dasharray: 785px;
  stroke-dashoffset: 785px;
  animation: alid 0.4s;
  animation-fill-mode: forwards;
  transform: rotate(-45deg);
  animation-delay: 0.2s;
}

@keyframes alid {
  from {
    stroke-dashoffset: 785px;
  }
  to {
    stroke-dashoffset: 0px;
  }
}
